import React from 'react';
import {Card, CardContent, CardHeader, Container, Grid,Typography} from '@material-ui/core';
import Button from '@material-ui/core/Button';
import GetAppOutlinedIcon from '@material-ui/icons/GetAppOutlined';
import { makeStyles } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar';
import Box from '@material-ui/core/Box';
import CropFreeIcon from '@material-ui/icons/CropFree';
import IconButton from '@material-ui/core/IconButton';
import Link from '@material-ui/core/Link';
import FileUploaded1 from '../Components/FileUploaded1';
import { useHistory } from "react-router-dom";


const useStyle=makeStyles({
    title:{
        textAlign:'center'
        
    },
    subtitle:{
        marginTop:'5%',
        textAlign:'center',
    },
    box:{
        border:'5px solid',
        borderColor:' #dfdede',
        marginTop:'1%',
        marginBottom:'2%',
        marginRight:'6%',
        marginLeft:'6%'
        
    },
   
    cardsubtitle:{
        textAlign:'center'
    },
    btnNew:{
        display:'flex',
        flexDirection: "row",
        justifyContent:"center",
        
    },
    card2:{
        margin:'2%'
    },
    btnfile:{
        color:'Black',
        backgroundColor:'#bae02c',
      
    },
    newCnnection:{
        textAlign:'center',
        fontWeight:'bolder',
        textDecoration:'underline',
        marginBottom:'15%',
        marginTop:'5%'
    },
   
    bntupload:{
        marginTop:'5%',
        backgroundColor:'orange',
        color:'#ffff'
        
    },
    uploadfiles:{
        marginTop:'2%'
    },
    exsitingTitle:{
        textAlign:'center',
        fontWeight:'bolder',
        marginTop:'5%',
    },
     grid:{
         borderRight:'5px solid #dfdede',
     },
     Card1:{
         margin:'2%'
     },
     btnConnect:{
        color:'Black',
        backgroundColor:'#bae02c',
        marginTop:'5%'
     },
     btnQRcode:{
        color:'Black',
        marginTop:'10%'
    
     },
     btnQRWord:{
         marginTop:'10%'
     },
     input:{
         color:'blue',
         textAlign:'center'
     },
     Footer:{
         textAlign:'center'
     }
     
    

});

export default function Home() {
   
    const history = useHistory();
    const navigateTo = () => history.push('/Room');
    const classes=useStyle();

    return (  

        <div>
            <AppBar>
            <Toolbar>
                <Typography variant='h5'>
                    Sharing App
                </Typography>
            </Toolbar>
         </AppBar>   

        <Grid container>
            <Grid item xs={12} md={12} lg={12} className={classes.subtitle}>
                <Typography  variant='h5'>
                    To Start Connecting and Sharing, Open New or Existing Network
                </Typography>
            </Grid>  

        </Grid>
        
      

        `<div className={classes.box}>

            <Grid container>
                <Grid item  xs={12} md={6} lg={6}  className={classes.grid}>
                    
                            <Typography
                            className={classes.newCnnection}
                            variant='h5'
                            >
                                New Connection
                            </Typography>
                            <Typography className={classes.cardsubtitle}>
                            Click on Create New Connection to start sharing between multiple devices or drag and drop files to start sharing automatically.
                            </Typography>

                            <Card className={classes.card2}>
                                <CardContent>
                                    <Box className={classes.btnNew}>
                                    <Button                                   
                                     variant='contained'
                                     className={classes.btnfile}   
                                     fullWidth  
                                     onClick={navigateTo}                             
                                    >Create New Connection </Button>
                                    </Box>
                                        <div className={classes.uploadfiles}>
                                       <FileUploaded1/>
                                        </div>                                   
                                    
                                    
                                </CardContent>
                            </Card>
                            
                </Grid>
                <Grid item  xs={12} md={6} lg={6}>
                    
                        <Typography
                            className={classes.newCnnection}
                            variant='h5'
                            >
                                Existing Connection
                            </Typography>
                            <Typography className={classes.cardsubtitle}>
                            If you have already opened existing connection, type your connection code, or scan QR Code to join the existing room
                            </Typography>

                            <Grid container>

                            <Grid item xs={12} md={6} lg={6}  className={classes.grid}>
                            <Typography
                            className={classes.exsitingTitle}
                            variant='h6'
                            >
                                Type your code
                            </Typography>

                            <Card className={classes.Card1} >
                                <CardContent>
                                <Box textAlign='center'>
                                <TextField 
                                    className={classes.input}
                                    label="00000"
                                    type="numeric"
                                    variant="outlined"
                                    color='primary'
                                    textAlign='center'
                                    fullWidth
                                /> 
                                <Button                                   
                                     variant='contained'
                                     className={classes.btnConnect}    
                                     fullWidth                              
                                    >Connect </Button>
                                </Box>


                                </CardContent>
                            </Card>
                    
                              
                            </Grid>

                            <Grid item  xs={12} md={6} lg={6}>
                            <Typography
                            className={classes.exsitingTitle}
                            variant='h6'
                            >
                                Scan QR Code
                            </Typography>

                            <Card className={classes.Card1} >
                                <CardContent>
                                <Box textAlign='center'>
                               
                                <Typography
                                    className={classes.btnQRWord}
                                    variant='body2'
                                    >
                                        This will require your Device Camera
                                    </Typography >
                                                                              
                                     <IconButton >
                                     <CropFreeIcon fontSize="large" />

                                   </IconButton>
                                </Box>


                                </CardContent>
                            </Card>
                              
                          </Grid>
                        </Grid>   



                </Grid>

            </Grid>

           

        </div>
       
        <Grid container>
                <Grid item xs={4} md={4} lg={4} className={classes.Footer}>
                <Typography>
                   <Link variant='subtitle1'  href="#" >
                   Contact Us
                    </Link>
                   </Typography>
                </Grid>
                <Grid item  xs={4} md={4} lg={4} className={classes.Footer}>

                <Typography>
                   <Link variant='subtitle1'  href="#" >
                   Privacy Policy
                    </Link>
                   </Typography>

                </Grid>

                <Grid item  xs={4} md={4} lg={4} className={classes.Footer}>
                    <Typography>
                    <Link variant='subtitle1' href="#" >
                    Terms of Use
                        </Link>
                    </Typography>

                    
                </Grid>

            </Grid>
        
         </div>
  
        
    )
}
