import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import {Box, Card, CardContent, CardHeader, Container, Divider, Grid, IconButton, Button,List, ListItem, ListItemAvatar, ListItemIcon, ListItemText, ListSubheader, Paper, TextField} from '@material-ui/core';
import { Drawer, makeStyles, Typography } from '@material-ui/core';
import { mergeClasses } from '@material-ui/styles';
import Avatar from '@material-ui/core/Avatar';
import CssBaseline from '@material-ui/core/CssBaseline';
import SettingsIcon from '@material-ui/icons/Settings';
import SendIcon from '@material-ui/icons/Send';
import FileUpload2 from '../Components/FileUpload2';
import GetAppOutlinedIcon from '@material-ui/icons/GetAppOutlined';
import DeleteIcon from '@material-ui/icons/Delete';
import importImg from '../Components/LNaG7A.png';
import LinkIcon from '@material-ui/icons/Link';
import CloudIcon from '@material-ui/icons/Cloud';
import LinearProgress from '@material-ui/core/LinearProgress';
import ImageIcon from '@material-ui/icons/Image';
import PublishIcon from '@material-ui/icons/Publish';



const drawerWidth=240

const useStyle=makeStyles((theme)=>{
    return{

        addFile:{
            
            boxShadow: 'none',
            textTransform: 'none',
            backgroundColor:'#ffff',
            border: '1px solid black',
            color:'black'
        },
        toolbar:{
            marginTop:'100px'
        },
        UsersCard:{
           margin:'5%'
        },
        orange:{
            backgroundColor:'orange'
        },
        gree:{
            backgroundColor:'green'
        },
        red:{
            backgroundColor:'red'
        },
        message:{
            marginTop:'2%',
            width:'75%'

        },
       
        sendButton:{
            margin: theme.spacing(1),
            marginTop:'20px',
            
            
        },
        messageList:{
           marginBottom:'5px',
           backgroundColor:'#E7F2F3'

        },
        messagesend:{
            display:'flex'
        },
        dropzone:{
            margin:'2%'
        },
        addDelete:{
            display:'inline-block',
            marginRight:'70%'
        },
        image:{
            width:'100px',
            height:'auto'
        },
        storageText:{
            marginTop:'10px'
        },
        getlink:{
            marginTop:'20px'
        },
        FilesListView:{
           
            display:'row'
           
            
        }
      
    }
   
});


export default function Room() {
    const classes=useStyle()
    
    const usersList=[
        {
            text:'User1',
            avator:<Avatar className={classes.orange}>N</Avatar>
            
        },
        {
            text:'User2',
            avator:<Avatar className={classes.green}>P</Avatar>
            
        },
        
       
    ]

    const message=[
        {
            id:'1',
            name:'user1',
            message:'Hey..!',
            person:<Avatar className={classes.orange}>N</Avatar>
        },
        {
            id:'2',
            name:'user2',
            message:'Hello How are you?',
            person:<Avatar className={classes.green}>P</Avatar>
        }
        
      
    ]

    const FilesList=[
        {
            id:'1',
            name:'File 1',
            icon:<ImageIcon fontSize='large'/>
        },
        {
            id:'2',
            name:'File 2',
            icon:<ImageIcon fontSize='large'/>
        },
        {
            id:'3',
            name:'File 3',
            icon:<ImageIcon fontSize='large'/>
        },
        {
            id:'4',
            name:'File 4',
            icon:<ImageIcon fontSize='large'/>
        },
        {
            id:'5',
            name:'File 5',
            icon:<ImageIcon fontSize='large'/>
        },
        {
            id:'6',
            name:'File 4',
            icon:<ImageIcon fontSize='large'/>
        },
        {
            id:'7',
            name:'File 5',
            icon:<ImageIcon fontSize='large'/>
        },
      
       
    ]

    return (
        <div>
        <AppBar>
            <Toolbar>
                <Typography variant='h5'>
                   Room
                </Typography>
            </Toolbar>
         </AppBar> 

         <div className={classes.toolbar}> 
            <Grid container>
                <Grid item xs={12} md={6} lg={4}>
                   <Card  className={classes.UsersCard}> 
                       <CardHeader
                       title={'Users Connected in Room'}
                       action={
                        <IconButton >
                         <SettingsIcon/>
                        </IconButton>
                    }
                       />
                       <CardContent>
                           <List>
                               {usersList.map(item=>(
                                   <ListItem button
                                   key={item.text}
                                   
                                   >
                                       <ListItemAvatar>{item.avator}</ListItemAvatar>
                                       <ListItemText>{item.text}</ListItemText>
                                       
                                </ListItem>
                               ))}
                              
                           </List>
                        
                       </CardContent>
                   </Card>
                   
                   <Card className={classes.UsersCard}>
                       <CardHeader
                       title={'Chat With Others'}
                       
                       />
                       
                       <CardContent>
                           
                           <List>
                               {message.map(({id,name,message,person})=>(
                                    <React.Fragment key={id}>
                                        <CssBaseline />
                                        <Paper className={classes.messageList}>
                                        <ListItem button >
                                           <ListItemAvatar>{person}</ListItemAvatar>
                                           <ListItemText primary={name} secondary={message} />
                                       </ListItem>
                                        </Paper>
                                      
                                    </React.Fragment>
                               ))}
                           </List>
                           
                           <Box className={classes.messagesend}>
                           <TextField variant='outlined' 
                           label='Enter Message here.'
                           className={classes.message}
                           />
                           <Button
                           className={classes.sendButton}
                           variant='contained'
                           color='primary'
                           startIcon={<SendIcon/>}
                           ></Button>
                           </Box>
                           
                       </CardContent>
                      
                   </Card>

                   <Card className={classes.UsersCard}>
                       <CardContent>
                           <Box flexDirection='row' display='flex'>
                           <CloudIcon fontSize='large' color='primary'/> &nbsp;&nbsp;
                           <Typography
                           variant='body1'
                           className={classes.storageText}
                           >
                              
                           Storage (7.2 GB out of 15 GB)
                           </Typography>
                          
                           </Box>
                           <Box marginTop='5px' width="100%" mr={1}>
                                <LinearProgress value='50' variant="determinate"/>
                            </Box>
                       </CardContent>

                   </Card>
                
                </Grid>
                <Grid item xs={12} md={6} lg={6}>
                    
                    <div className={classes.dropzone}>
                    <div >
                        <Box textAlign='right'>
                            <Button
                            variant='contained'
                            size='small'
                            className={classes.addFile}
                            color='primary'
                            startIcon={<PublishIcon/>}
                            >Add File</Button>

                            <IconButton aria-label="delete" color='secondary'
                            >
                                <DeleteIcon fontSize="large"/>
                            </IconButton>
                        </Box>
                    </div>
                    
                    <Box scrolly='true' height='50%' border='4px solid #E2DEDE'>
                    <FileUpload2/>
                    <Grid item sm={12} md={6} lg={4}>
                        <div >
                            <List>
                                
                                {FilesList.map((item)=>(
                                    
                                    <ListItem
                                
                                    button
                                    key={item.id}
                                    >
                                        
                                        <ListItemIcon>{item.icon}</ListItemIcon>
                                        <ListItemText>{item.name}</ListItemText>
                                    </ListItem>

                                ))}
                            
                            </List>
                        </div>
                    </Grid>
                   
                    </Box>
                   
         
                    </div>
                   
                                
                </Grid>

                <Grid item item xs={12} md={6} lg={2}>
                    <Box marginTop='10%' textAlign='center'>
                    <Typography
                    variant='body1'
                    >
                    Share or Add Users to the Room
                    </Typography>

                    </Box>
                    
                    <Card  className={classes.dropzone}>
                        <CardContent>
                        <Box textAlign='center'>
                        <Typography
                        variant='h5'
                        color='primary'
                        >
                        Room Code
                        </Typography>

                        <Typography
                        variant='h6'
                        color='Secondary'
                        >
                        13AB7Z
                        </Typography>

                    </Box>
                        </CardContent>
                    </Card>

                    <Card  className={classes.dropzone}>
                        <CardContent>
                        <Box textAlign='center'>
                        <Typography
                        variant='h6'
                        color='primary'
                        >
                        Scan Below QR to invite someone to your room
                        </Typography>

                        <img className={classes.image} src={importImg}/>

                        <Button
                        className={classes.getlink}
                        variant="contained"
                        color='primary'
                        startIcon={<LinkIcon/>}
                        fullWidth
                        >
                            Get Link
                        </Button>

                    </Box>
                        </CardContent>
                    </Card>
                </Grid>
                
            </Grid>


         </div>
   
        </div>
    )
}
